import React from 'react';
import { Link } from '@reach/router';

const PrefixedLink = ({
  to,
  children,
  ...props
}) => {
  const url = to.startsWith('/') ? `${process.env.PUBLIC_URL}${to}` : to;
  return (
    <Link {...props} to={to} >
      {children}
    </Link>
  );
};

export default PrefixedLink;
